body{
  margin: 0 !important;
}

.site-header {
  text-align: center;
  width: 100%;
  height: 40px !important;
  padding-top: 5px;
  background: #3c6996;
  color: white;
  span {
    font-weight: bold;
  }
}

.fill-page-container {
  overflow: scroll;
  position: absolute;
  width: 100%;
  height: calc(100% - 40px);
  left: 0;
  margin: 0;
}

.user-section {
  padding: 2px;
  width: 200px;
  height: 30px;
  background: rgba(0, 0, 0, .12);
  display: flex;
  right: 30px;
  top: 5px;
  position: absolute;
  border-radius: 5px;
  img {
    height: 25px;
    width: 25px;
  }
  span {
    margin-left: 10px;
  }
  i {
    margin-top: 5px;
    right: 15px;
    position: absolute;
  }
}